import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { Modal, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import i18n from "../i18n";
export const Security = ({ isOpen, onClose, children }) => {
  const navigate = useNavigate();
  const [step, setStep] = useState("");
  const [stepcheck, setStepcheck] = useState("");
  const [showModalTwofactor, setshowModalTwofactor] = useState(false);
  const [showModalTwofactorPass, setshowModalTwofactorPass] = useState(false);
  const [messages, setmessage] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const [showPopSuccess, setShowPopSuccess] = useState(false);
  const [showPopError, setShowPopError] = useState(false);
  const [successPopMessage, setPopsuccessMessage] = useState("");
  const [errorPopMessage, setPoperrorMessage] = useState("");
  const [userdata, setuserdata] = useState("");
  const { t } = useTranslation();

  useEffect(() => {
    const savedLanguage = localStorage.getItem("language") || "en"; // Default to 'en' if no language is saved
    // Change the language if it's not already the current language
    if (i18n.language !== savedLanguage) {
      i18n.changeLanguage(savedLanguage);
    }
  }, [i18n, t]);
  const handleModalClose = async () => {
    setshowModalTwofactor(false);
    setshowModalTwofactorPass(false);
  };
  const apiUrl = "https://backend.amourette.no/api/members/";
  useEffect(() => {
    getUserDetail();
  }, []);
  const getUserDetail = async () => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    let formData = {
      user_id: userLogin.id,
    };
    try {
      const res = await axios.post(apiUrl + "getUserDetail", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.row[0];
      console.log(respo);
      setStep(respo.two_fA);
      setStepcheck(respo.two_fA);
      setuserdata(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    let formData = {
      otp: e.target.otp.value,
      id: userLogin.id,
    };
    try {
      const res = await axios.post(apiUrl + "checkOTP", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data;
      console.log(respo);
      if (respo.status === "2") {
        setShowPopError(true);
        setPoperrorMessage(respo.message);
        setTimeout(() => {
          setShowPopError("");
        }, 1000);
      } else {
        setStepcheck(step);
        setShowPopSuccess(true);
        setPopsuccessMessage(respo.message);
        setTimeout(() => {
          setshowModalTwofactor(false);
          setPopsuccessMessage("");
        }, 1500);
      }
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const handleSubmitpassword = async (e) => {
    e.preventDefault();
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    let formData = {
      password: e.target.password.value,
      id: userLogin.id,
    };
    try {
      const res = await axios.post(apiUrl + "checkpassword", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data;
      console.log(respo);
      if (respo.status === "2") {
        setShowPopError(true);
        setPoperrorMessage(respo.message);
        setTimeout(() => {
          setShowPopError("");
        }, 1000);
      } else {
        sendotpPas();
        setShowPopSuccess(true);
        setPopsuccessMessage(respo.message);
        setTimeout(() => {
          setshowModalTwofactor(true);
          setshowModalTwofactorPass(false);
          setPopsuccessMessage("");
        }, 1500);
      }
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const sendotpPas = async () => {
    if (step !== "") {
      const storedUsername = localStorage.getItem("userLogin");
      const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
      let formData = {
        id: userLogin.id,
        two_fA: step,
      };
      try {
        const res = await axios.post(apiUrl + "sendOTP", formData, {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        });
        var respo = res.data;
        console.log(respo);
        if (respo.status === "2") {
          setShowPopError(true);
          setPoperrorMessage(respo.message);
          setTimeout(() => {
            setShowPopError("");
          }, 1000);
        } else {
          setShowPopSuccess(true);
          setPopsuccessMessage(respo.message);
          setTimeout(() => {
            setPopsuccessMessage("");
          }, 1500);
        }
      } catch (err) {
        // Enhanced error handling
        if (err.response) {
        } else if (err.request) {
          console.error("Request data:", err.request);
        } else {
          console.error("Error message:", err.message);
        }
      }
    }
  };
  const handleclick = async (vl) => {
    setStep(vl);
    if (vl !== "") {
      if (vl === "Yes") {
        setshowModalTwofactorPass(true);
      }
    }
  };
  const handleOverlayClick = (e) => {
    if (e.target.classList.contains("popup-overlay")) {
      onClose();
    }
  };
  const [successpass, setsuccesspass] = useState("");
  const [errorpasscurr, seterrorpasscurr] = useState("");
  const [errorpass, seterrorpass] = useState("");
  var apiURL = "https://backend.amourette.no/api/";
  const isValidPassword = (password) => {
    // Check minimum length and strong password criteria
    const minLength = 8;
    const hasUpperCase = /[A-Z]/.test(password);
    const hasLowerCase = /[a-z]/.test(password);
    const hasDigits = /\d/.test(password);
    const hasSpecialChar = /[!@#$%^&*(),.?":{}|<>]/.test(password);

    return (
      password.length >= minLength &&
      hasUpperCase &&
      hasLowerCase &&
      hasDigits &&
      hasSpecialChar
    );
  };
  const handleSubmitForgotpassword = async (e) => {
    e.preventDefault();
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    seterrorpasscurr("");
    let formData = {
      newpassword: e.target.newpassword.value,
      currentpassword: e.target.currentpassword.value,
      confirmpassword: e.target.confirmpassword.value,
      id: userLogin.id,
    };
    if (
      !e.target.newpassword.value ||
      !isValidPassword(e.target.newpassword.value)
    ) {
      seterrorpass(
        "Password must be at least 8 characters long and include uppercase letters, lowercase letters, digits, and special characters."
      );
      return;
    } else {
      seterrorpass(""); // Clear error if valid
    }
    console.log(e.target.newpassword.value, e.target.confirmpassword.value);
    if (e.target.newpassword.value !== e.target.confirmpassword.value) {
      seterrorpass("New and retype password does not match");
      setsuccesspass("");
    } else {
      try {
        const res = await axios.post(
          apiURL + "forgotPasswordmannual",
          formData,
          {
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json",
            },
          }
        );
        var check = res.data;
        console.log(check);
        if (check.status === "2") {
          seterrorpasscurr("Current password does not match");
          setsuccesspass("");
        } else {
          seterrorpass("");
          seterrorpasscurr("");
          setsuccesspass("Your password has been successfully changed");
          e.target.reset();
          setTimeout(() => {
            seterrorpass("");
            seterrorpasscurr("");
            setsuccesspass("");
          }, 2000);
        }
      } catch (err) {}
    }
  };
  if (!isOpen) return null;
  return (
    <>
      <div className="d-flex flex-column gap-4 mship">
        <h1 className="shiptitle">{t("Security")}</h1>

        <div className="d-flex flex-column gap-3">
          <div className="blockv d-flex flex-column gap-4">
            <h3>{t("We_Offer_a")}</h3>
            <div className="d-flex flex-column gap-3">
              <h4>{t("Security_question")}</h4>
              <p>{t("To_protect")}</p>
              <div className="listbox d-flex flex-column gap-2">
                <label className="cbox">
                  {t("Yes")}
                  <input type="radio" name="Security" />
                  <span className="checkmark"></span>
                </label>
                <label className="cbox">
                  {t("No")}
                  <input type="radio" name="Security" />
                  <span className="checkmark"></span>
                </label>
              </div>
            </div>
            <div className="d-flex flex-column gap-3">
              <h4>{t("two_factor")}</h4>
              <p> {t("Enhance_your_account")}</p>
              <div className="listbox d-flex flex-column gap-2">
                <label className="cbox">
                  {t("Yes")}
                  <input
                    type="radio"
                    name="factor"
                    value="Yes"
                    checked={stepcheck === "Yes"}
                    onChange={() => handleclick("Yes")}
                  />
                  <span className="checkmark"></span>
                </label>
                <label className="cbox">
                  {t("No")}
                  <input
                    type="radio"
                    name="factor"
                    value="No"
                    checked={stepcheck === "No"}
                    onChange={() => handleclick("No")}
                  />
                  <span className="checkmark"></span>
                </label>
              </div>
            </div>
            <div className="d-flex flex-column gap-3">
              <h4>{t("Personal_data")}</h4>
              <p>{t("Your_privacy_matters")}</p>
              <div className="listbox d-flex flex-column gap-2">
                <label className="cbox">
                  {t("Yes")}
                  <input type="radio" name="factor" />
                  <span className="checkmark"></span>
                </label>
                <label className="cbox">
                  {t("No")}
                  <input type="radio" name="factor" />
                  <span className="checkmark"></span>
                </label>
              </div>
              <div className="d-block shipbtns">
                <div className="col-md-4">
                  <button type="button" className="btn celbtn">
                    {t("REQUESTANEXPORT")}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="d-flex flex-column gap-3 security_paas">
          <div className="blockv d-flex flex-column gap-3">
            <h3>{t("Passwordmanagement")}</h3>
            <div className="d-flex flex-column gap-3 ">
              <p>
                Lorem Ipsum is simply dummy text of the printing and typesetting
                industry. Lorem Ipsum has been the industry's standard dummy
                text ever since the 1500s, when an unknown printer took a galley
                of type and scrambled it to make a type specimen book.
              </p>
              <form onSubmit={handleSubmitForgotpassword} method="post">
                <div className="row gy-3">
                  <div className="col-12">
                    <div className="d-flex flex-column gap-2">
                      <input
                        type="password"
                        name="currentpassword"
                        required
                        placeholder={t("CurrentPassword")}
                      />
                    </div>
                    <span className="text-danger">{errorpasscurr}</span>
                  </div>
                  <div className="col-12">
                    <div className="d-flex flex-column gap-2">
                      <input
                        type="password"
                        name="newpassword"
                        required
                        placeholder={t("NewPassword")}
                      />
                    </div>
                  </div>
                  <div className="col-12">
                    <div className="d-flex flex-column gap-2">
                      <input
                        type="password"
                        name="confirmpassword"
                        required
                        placeholder={t("retypePassword")}
                      />
                    </div>
                    <span className="text-danger">{errorpass}</span>
                    <span className="text-success">{successpass}</span>
                  </div>
                  <div className="col-12">
                    <div className="d-flex flex-column gap-3">
                      <Link to="/forgot-pasword">{t("forgotp")}</Link>

                      <button type="submit" className="regbtnsubmit">
                        {t("save_changes")}
                      </button>
                    </div>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      <Modal
        show={showModalTwofactorPass}
        className="edit_admin"
        onHide={handleModalClose}
      >
        <Modal.Header closeButton>
          <Modal.Title className="textcolor">{t("EnterPassword")}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form onSubmit={handleSubmitpassword}>
            {showPopSuccess && (
              <span className="text-success">{successPopMessage}</span>
            )}
            {showPopError && (
              <span className="text-danger">{errorPopMessage}</span>
            )}
            <div className="mb-3">
              <label htmlFor="username" className="form-label">
                {t("Password")}
              </label>
              <input
                type="text"
                required
                className="form-control"
                id="password"
                name="password"
                placeholder={t("EnterPassword")}
              />
            </div>

            <Modal.Footer>
              <Button variant="secondary" onClick={handleModalClose}>
                {t("Close")}
              </Button>
              <Button
                type="submit"
                variant="primary"
                className="bg-success border-color-none"
              >
                {t("save_changes")}
              </Button>
            </Modal.Footer>
          </form>
        </Modal.Body>
      </Modal>
      <Modal
        show={showModalTwofactor}
        className="edit_admin"
        onHide={handleModalClose}
      >
        <Modal.Header closeButton>
          <Modal.Title className="textcolor">{t("Enterotp")}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form onSubmit={handleSubmit}>
            {showPopSuccess && (
              <span className="text-success">{successPopMessage}</span>
            )}
            {showPopError && (
              <span className="text-danger">{errorPopMessage}</span>
            )}
            <div className="mb-3">
              <label htmlFor="username" className="form-label">
                {t("OTP")}
              </label>
              <input
                type="text"
                required
                className="form-control"
                id="username"
                name="otp"
                placeholder={t("Enterotp")}
              />
            </div>

            <Modal.Footer>
              <Button variant="secondary" onClick={handleModalClose}>
                {t("Close")}
              </Button>
              <Button
                type="submit"
                variant="primary"
                className="bg-success border-color-none"
              >
                {t("save_changes")}
              </Button>
            </Modal.Footer>
          </form>
        </Modal.Body>
      </Modal>
    </>
  );
};
